	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"[BlogTalk] Updated List of Conference Bloggers","date":"24 May 2003","slug":"/2003-05-24-blogtalk-updated-list-of-conference-bloggers/","tags":"Y2003"},"html":"<p><a title=\"Stop blogging and start paying attention!\" href=\"http://www.hyperorg.com/blogger/mtarchive/001542.html\">Joho the Blog: BlogTalk List of Conference Bloggers</a></p>"},"next":{"fields":{"title":"Sitting In The Back Row","date":"23 May 2003","slug":"/2003-05-23-sitting-in-the-back-row/"}},"previous":{"fields":{"title":"Drugstore of Hope","date":"24 May 2003","slug":"/2003-05-24-drugstore-of-hope/"}}},{"node":{"fields":{"title":"Sitting In The Back Row","date":"23 May 2003","slug":"/2003-05-23-sitting-in-the-back-row/","tags":"Y2003"},"html":"<p>As promised, I'm in the back row at BlogTalk, wearing my dead fish cap, next to a power outlet so I can fire up my little ThinkPad. And the WiFi seems to work. </p>\n<p>I'm skeptical of most of the points brought up by the panelists so far. How can bloggers internationally maintain any sort of meaningful dialouge given the wide cultural and language differences? How can any kind of semantic markup keep up with the dynamic interests and specialties of the people who write the blogs? And why don't they have any coffee at this conference center?</p>\n<p>By the way, both Heiko and Haiko are pronounced HIGH-ko, but they are easy to tell apart. Heiko is the one who can't read the map, and Haiko is the one who <s>can't find</s> never uses the brakes.</p>"},"next":{"fields":{"title":"Blogging Around the World","date":"23 May 2003","slug":"/2003-05-23-blogging-around-the-world/"}},"previous":{"fields":{"title":"[BlogTalk] Updated List of Conference Bloggers","date":"24 May 2003","slug":"/2003-05-24-blogtalk-updated-list-of-conference-bloggers/"}}},{"node":{"fields":{"title":"Blogging Around the World","date":"23 May 2003","slug":"/2003-05-23-blogging-around-the-world/","tags":"Y2003"},"html":"<p>The second set of presentations is somewhat more relevant and interesting. Presentators from Iran, Poland, and Spain are explaining about the role of blogs in their countries, which are more social, communicative, and <i>friendly</i> than the world the technologists were talking about this morning. I know which world I'd rather be a part of.</p>"},"next":{"fields":{"title":"TypePad News: Some Frequently Asked Questions","date":"22 May 2003","slug":"/2003-05-22-typepad-news-some-frequently-asked-questions/"}},"previous":{"fields":{"title":"Sitting In The Back Row","date":"23 May 2003","slug":"/2003-05-23-sitting-in-the-back-row/"}}}],"pathPrefix":"","first":false,"last":false,"index":726,"pageCount":1101}}

